    <div class="home_content">
      <div class="post_category trans_200" v-if="currentArticle.category !== undefined">
        <router-link class="trans_200" :to="'/categories/' + currentArticle.category.id">{{currentArticle.category.name}}</router-link>
      </div>
      <div class="post_title">{{currentArticle.title}}</div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  beforeRouteUpdate (to, from, next) {
    if (Object.is(Number(to.params.id), NaN)) {
      window.toastr.error('非法路由地址')

      next({ path: '/home' })
    } else {
      this.fetchArticle(to.params.id)

      next()
    }
  },

  data () {
    return {
    }
  },

  computed: {
    ...mapState(['currentArticle'])
  },

  mounted () {
    this.fetchArticle(this.$route.params.id)
  },

  methods: {
    ...mapActions({
      fetchArticle: 'getArticleBy'
    })
  }
}
</script>

